.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  background-color: #68B738;
  color: #ffffff;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-decoration: none;
}

.page {
  font-family: "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
}

.page__body {
  margin: 0;
}

.page-main__heading {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
}

.page-main__subheading {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
}

.site-menu {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 20px;
  line-height: 1;
  text-align: center;
  text-transform: uppercase;
  list-style: none;
}

.site-menu__link {
  color: #000000;
  text-decoration: none;
}

.promo {
  font-family: "Oswald", "Arial", sans-serif;
  color: #ffffff;
  font-size: 14px;
  line-height: 14px;
  text-align: center;
  background-image: url("../../img/background/promo-bg.jpg");
  background-color: #68B738;
  background-repeat: repeat;
}

.promo__button {
  text-decoration: none;
  color: #ffffff;
}

.promo__slogan {
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
}

.promo__subslogan {
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
}

.catalog__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.card__link {
  color: #222222;
  text-decoration: none;
}

.card__heading {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}

.card__values {
  color: #444444;
  font-size: 12px;
  line-height: 16px;
}

.form {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #444444;
}

.form__necessary {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  text-transform: none;
  color: #000000;
}

.field-group {
  border: none;
}

.field-group__heading {
  font-size: 24px;
  line-height: 26px;
  text-transform: none;
  color: #000000;
}

.field-group__list {
  list-style-type: none;
}

.field-group--cat-info {
  color: #444444;
}

.show-more__heading {
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  line-height: 20px;
  color: #111111;
}

.show-more__text {
  font-size: 12px;
  line-height: 16px;
  color: #444444;
}

.show-more__button {
  font-family: "Oswald", "Arial", sans-serif;
  text-decoration: none;
  font-size: 16px;
  line-height: 20px;
  background-color: #f2f2f2;
  color: #444444;
}

.catalog-additional__heading {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 24px;
  font-weight: 400;
}

.catalog-additional__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.catalog-additional__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 16px;
  font-weight: 400;
}

.catalog-additional__description {
  font-size: 14px;
  line-height: 14px;
  color: #444444;
}

.catalog-variety__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.catalog-variety__item {
  background-color: #f2f2f2;
}

.catalog-variety__heading {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 37px;
  text-transform: uppercase;
}

.catalog-variety__description {
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}

.catalog-variety__link {
  font-family: "Oswald", "Arial", sans-serif;
  line-height: 16px;
  text-decoration: none;
  color: #000000;
  text-transform: uppercase;
}

.explanation__heading {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
}

.explanation__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.explanation__description {
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}

.example {
  background-color: #eaeaea;
}

.example__heading {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
}

.example__description {
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}

.example__definition {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 24px;
}

.example__title {
  font-size: 12px;
  line-height: 12px;
}

.example__cost {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
}

.switcher__button {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
}

.partnership__heading {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  text-transform: uppercase;
}

.partnership__adress {
  font-size: 14px;
  line-height: 20px;
  color: #444444;
}

.social-media {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.developer {
  color: #444444;
  text-decoration: none;
}